.button {
    width: 60%;
    font-size: 1.1rem;
    padding: 0.5em 1em;
    margin-left: auto;
    margin-right: auto;
    background: #647b43;
    color: white;
    border: none;
    border-radius: 4px;
    text-align: center;
    &:hover {
      background: linear-gradient(#647b43, #2d381e);
    }
  }